}

.btn{
    border: 1px solid cyan;
    border-radius: 8px;
    line-height: 48px;
    /* background: cyan; */
    width: 20%;
    color: white;
    font-size: 20px;
    text-align: center;
    transition: .3s ease-in-out;
    background-color: #081b29;
    box-shadow: 5px 6px 6px cyan ;

}
