
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    text-decoration: none;
    font-family: 'poppins', sans-serif;
    
    font-family: 'poppins',sans-serif;
    
}






#contact-us{
  width: 100%;
  margin: auto;
  padding-top: 40px;
}
.contact-col{
    flex-basis: 48%;
    margin-bottom: 30px;
}
.contact-col div{
    display: flex;
    margin-bottom:  40px;
   align-items: center;
}
.contact-col div i{
    font-size: 28px;
    color: cyan;
    margin: 10px;
    margin-right: 40px;
}
.contact-col input, .contact-col textarea{
    color: white;
    background-color: #081b29;
    width: 100%;
    padding: 10px;
    margin-bottom: 17px;
    outline: none;
    border: 1px solid cyan;
}
.contact-col div h2{
    color: #8b8080;
}
#btn{
    line-height: 48px;
    margin: auto;
    width: 40%;
    font-size: 24px;
   color: #ccc;
    background-color: #081b29;
    border: 1px solid cyan;
  text-align: center;
  border-radius: 8px;
  transition: .3s ease-in-out;
}
#btn:hover{
    background-color: #081b29;
    box-shadow: 5px 6px 6px 9px cyan ;
}