
*{
  font-family: 'poppins',sans-serif;
}


.row{
  margin: 5%;
  display: flex;
  justify-content: space-between;
}
.col{
  margin-bottom: 5%;
  padding: 20px 10px;
  flex-basis: 31%;
}
.Experience-intro  {
  width: 80%;
  height: 40%;
  text-align: center;
  padding-top: 30px;
  margin: auto;
  /* background-color: cyan; */
  background-color: #081b29;
  box-shadow: 5px 6px 6px 9px cyan ;
}
.heading{
  font-size: 2rem;
  text-transform: capitalize;
}
h4{
  font-size: 24px;
  margin-bottom: 5%;
}
.col i{
  color: cyan;
  font-size: 20px;
  margin: auto;

}
.col p{
  font-size: 18px;
  line-height: 22px;
}
@media(max-width:700px){
  .row{
    flex-direction: column;
  }
}