
*{
    font-family: 'poppins',sans-serif;
}
#intro{
    height: 100%;
    width: 100vw;
    max-width: 75%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    font-size: 24px;
    font-weight: 700;
    /* overflow: hidden; */
    margin: 0 auto;
}

img{
    z-index: -1;
    height: 300px;
    width: 300px;
    object-fit: cover;
    position: absolute;
    top: 4rem;
    right: 0;
    border-radius: 50%;
}

#intro-content{
    height: 100%;
    width: 100%;
    padding: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
}

#text-1{
    font-size: 18px;
    color: yellowgreen;
    margin-bottom: 5px;
}
h2{
    font-size: 34px;
    margin-bottom: 5px;

}
p{
    font-size: 18px;
    letter-spacing: 1px;
}
span{
    color: yellowgreen;
}

@media(max-width:700px){
    h2{
        font-size: 25px;
    }

 
     
    
}

.social-content{
    padding: 20px;
    
}
.social-content i{
    align-content: center;
    object-fit: cover;
    margin: 0.9rem;
    /* color: yellowgreen; */
   border-radius: 90%;
    /* background-color: cyan; */
    background-color: #081b29;
    box-shadow: 5px 6px 6px cyan ;
    width: 60px;
    height: 60px;
    padding: 20px;
    transition: .3s;
}




