


*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    text-decoration: none;
    font-family: 'poppins', sans-serif;
    
    font-family: 'poppins',sans-serif;
    
}




nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 90px;
  


}

:root{

--blue:#010513;
--s-blue:#596FB7;
--blur:#C6CF9B;
--slur:#F6ECA9;
--ms:#0081C9;
--jsx:#5BC0F8;
--dd:#86E5FF;
--ddl:#FFC93C;
}
body{
    background-color: #081b29;
    color: #ffff;
    z-index: 1000;
}

#navbar {
    display: flex;
    justify-content: center;
    align-items: center;
}

#navbar li{
    list-style: none;
    padding: 0 50px;
    position: relative;
}
#navbar li a {
    color: #fff;
    text-transform: capitalize;
    font-size: 24px;
}
#navbar li a:hover,#navbar li a.active
{
   color: cyan;
    transition: .3s ease-in-out;
}

.mobile i{
    display: none;
}
@media(max-width:700px){
    #navbar {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        position: fixed;
        top: 90px;
        right: -300px;
        background-color: #081b29;
        height: 100vh;
        width: -300px;
        padding: 40px 0 0 10px;
        transition: .3s ease-in-out;
    }

    #navbar li{
        margin-bottom: 25px;
    }
    .mobile i{
        display: block;
        font-size: 24px;
        margin: auto;
        cursor: pointer;
    }
    #navbar.active{
        right: 0;
    }
}
